<script>
import MoneyIcon from "@/@core/components/svg-icons/MoneyIcon.vue";
import SearchInput from "@/@core/components/search-input/SearchInput.vue";
import Modal from '@core/components/Modal.vue'
import {
	BButton,
	BAvatar,
	BCard,
	BAlert,
	BTable,
	BCollapse,
	BSpinner,
} from "bootstrap-vue";
import { v4 as uuidv4 } from "uuid";
import ListIcon from "@/@core/components/svg-icons/ListIcon.vue";
import { generateBgForBox } from "@/@core/utils";
import managerService from "@/api/managerService";
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

export default {
	components: {
		BButton,
		BAvatar,
		BCard,
		BAlert,
		SearchInput,
		MoneyIcon,
		BTable,
		ListIcon,
		BCollapse,
		BSpinner,
		Modal
	},
	data() {
		return {
			loading: false,
			deletePending: false,
			showDeleteModal: false,
			searchInput: "",
			debtorListFields: ["name", "fee"],
			debtorsList: [],
			units: [],
			selected: {},

			error: false,
		};
	},
	computed: {
		filteredDeptorList() {
			if (this.searchInput.trim() !== "") {
				const newDeptorList = this.units.filter((unit) => unit.name?.includes(this.searchInput));
				return newDeptorList;
			}
			return this.units;
		},
	},
	watch: {},
	async created() {
await this.loadData()
},
	methods: {
		generateBgForBox,
		async loadData() {
		try {
			this.loading = true;
			const { data: UserDebt } = await this.$http.get("manager/income", {
        headers: {
          Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
      });

			this.units = UserDebt.debtor_list.list;
			// console.log(UserDebt);

			this.loading = false;
		} catch (err) {
			this.loading = false;
			console.log(err);
		}
	},
		showDeleteModalfunc(selected) {
      this.showDeleteModal = true
      this.selected = selected
    },
	async ContinueDeleting(selected) {
      this.deletePending = true
      const data = { income_id: selected.income_id }
      await managerService.userDebtDelete(data).then(res => {
        this.$toast({
          component: ToastificationContent,
          props: {
            title: res.data.message,
            icon: 'EditIcon',
            variant: 'success',
          }
        })
        this.deletePending = false
        this.showDeleteModal = false
        this.units = this.units.filter(item => item.income_id != selected.income_id)
		this.loadData()
      })
	},
		getUniqueString() {
			return uuidv4();
		},
	},
};
</script>

