<!-- eslint-disable vue/html-indent -->

<template>
	<b-card>
		<magnifier class="magnifier-icon" />
		<b-form-input
			v-model="edit"
			class="search-input"
			type="search"
			placeholder="جستجو"
			:required="required"
		/>
	</b-card>
</template>

<script>
import { BCard, BFormInput } from "bootstrap-vue";
import Magnifier from "../svg-icons/Magnifier.vue";

export default {
	components: {
		BCard,
		BFormInput,
		Magnifier,
	},
	props: {
		value: {
			type: String,
			required: true,
		},
		required: {
			type: Boolean,
			default: false,
		},
	},
	computed: {
		edit: {
			get() {
				return this.value;
			},
			set(e) {
				this.$emit("input", e);
			},
		},
	}
};
</script>

